import {Component, Inject, PLATFORM_ID} from "@angular/core";
import {isPlatformBrowser} from "@angular/common";

@Component({
    selector: 'jumbotron',
    templateUrl: 'jumbotron.html'
})
export class JumbotronControl {
    private intervalId;

    jumbos: Jumbo[] = jumbos;

    ngOnInit() {
        if (isPlatformBrowser(this.platformId)) {
        this.intervalId = setInterval(() => {
            showNextJumbo();
        }, 5000);
        }
    }

    ngOnDestroy() {
        if (this.intervalId) {
            clearInterval(this.intervalId);
        }
    }
    constructor(@Inject(PLATFORM_ID) private platformId: Object){}
}

let jumbos: Jumbo[] = [
    {
        title: '     ',
        text: '   ,       .   ',
        icon: 'fa-phone',
        image: 'slide-1',
        show: true
    },
    {
        title: '     ',
        text: '    :  ,   ,  ,     ',
        icon: 'fa-thumbs-o-up',
        image: 'slide-2',
        show: false
    },
    {
        title: ',      ',
        text: '        .   !',
        icon: 'fa-smile-o',
        image: 'slide-3',
        show: false
    }
];

let activeJumbo = 0;

function showNextJumbo() {
    if (activeJumbo < jumbos.length - 1) {
        activeJumbo += 1;
    }
    else {
        activeJumbo = 0;
    }
    jumbos.forEach((jumbo, idx) => {
        jumbo.show = activeJumbo == idx;
    });
    return jumbos;
}

class Jumbo {
    title: string;
    text: string;
    icon: string;
    image: string;
    show: boolean;
}


